import React from 'react'
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import ProgressBar from './ProgressBar';
import Particle from '../Components/Particle';

function Skills() {
    return (
        <SkillsStyled>
            {/* <div className="particle-con">
                <Particle />
            </div> */}
                <Title title={'My Skills'} span={'my skills'} />
                <InnerLayout>
                    <div className="skills">
                        <p>Expert in Front-End development including technologies like HTML5, CSS3, JAVASCRIPT, JQUERY, ANGULAR, REACT, TYPESCRIPT, JASMINE, BOOTSTRAP, SASS, GIT, NODEJS etc.</p>
                        {/* <ProgressBar 
                            title={'HTML5'}
                            width={'70%'}
                            text={'70%'}
                        />
                        <ProgressBar 
                            title={'CSS3'}
                            width={'75%'}
                            text={'75%'}
                        />
                        <ProgressBar 
                            title={'JAVASCRIPT'}
                            width={'70%'}
                            text={'70%'}
                        />
                        <ProgressBar 
                            title={'JAVA'}
                            width={'60%'}
                            text={'60%'}
                        />
                        <ProgressBar 
                            title={'TypeScript'}
                            width={'75%'}
                            text={'75%'}
                        />
                       <ProgressBar 
                            title={'MySql'}
                            width={'85%'}
                            text={'85%'}
                        />
                        <ProgressBar 
                            title={'React JS'}
                            width={'75%'}
                            text={'75%'}
                        />
                        <ProgressBar 
                            title={'Node JS'}
                            width={'75%'}
                            text={'75%'}
                        /> */}
                    </div>
                </InnerLayout>
        </SkillsStyled>
    )
}

const SkillsStyled = styled.section`
    .skills{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 2rem;
        grid-column-gap: 3rem;
        @media screen and (max-width: 700px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
`;

export default Skills;
