import React from 'react';
import styled from 'styled-components';
import {MainLayout, InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import PrimaryButton from '../Components/PrimaryButton';
import PhoneIcon from '@material-ui/icons/Phone';
import EmailIcon from '@material-ui/icons/Email';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import ContactItem from '../Components/ContactItem';
import emailjs from 'emailjs-com';
import Particle from '../Components/Particle';

function ContactPage() {
    const phone = <PhoneIcon />
    const email = <EmailIcon />
    const location = <LocationOnIcon />
   const sendEmail = (e) => {
        e.preventDefault();
        emailjs.sendForm('service_2a3oqlj', 'template_b1n1apj',e.target, 'user_m0QbHLyngrK43X13Bg2mJ')
        .then(res => {
            console.log(res);
        }).catch(err => console.log(err));
        // e.target.message('Successfully Submitted')
        e.target.reset();
    }
    return (
        <div className="particle-con">
                <Particle />
        <MainLayout>
            
            
            <ContactPageStyled >
            
            <div className='typography'>
            <Title title={'Contact'} span={'Contact'} />
            <InnerLayout className={'contact-section'}>
                <div className="left-content">
                    <div className="contact-title">
                        <h4>Get In Touch</h4>
                    </div>
                    <form  className="form" onSubmit={sendEmail}>
                        <div className="form-field">
                            <label htmlFor="Name"  >Enter your name*</label>
                            <input name= "name" type="text" id="name" />
                        </div>
                        <div className="form-field">
                            <label htmlFor="Email"  >Enter your email*</label>
                            <input name= "email" type="email" id="email" />
                        </div>
                        <div className="form-field">
                            <label htmlFor="Subject"  >Enter your subject</label>
                            <input name= "subject" type="text" id="subject" />
                        </div>
                        <div className="form-field">
                            <label htmlFor="text-area">Enter your Message*</label>
                            <textarea name="textarea" id="textarea" cols="30" rows="10"></textarea>
                        </div>
                        <PrimaryButtonStyled >
                            {/* <label htmlFor="submit">Send Mail</label> */}
                            <input type="submit" id="button" 
                            // style={`background-color: var(--primary-color), color: white`}
                            />
                        </PrimaryButtonStyled>
                        {/* <div className="form-field f-button">
                            <button title={'Send Email'} htmlFor= 'submit'
                            onClick={sendEmail}
                            />
                        </div> */}
                    </form>
                </div>
                <div className="right-content">
                    <ContactItem title={'Phone'} icon={phone} cont1={'+91-9057469001'}  />
                    <ContactItem title={'Email'} icon={email} cont1={'tanujain8319@gmail.com'} cont2={'2018pcecstanu166@poornima.org'} />
                    <ContactItem title={'Address'} icon={location} cont1={'Pratap Nagar, Jaipur, Rajasthan'} cont2={'India'} />
                    
                </div>
            </InnerLayout>
            </div>
            </ContactPageStyled>
            
        </MainLayout>
        </div>
    )
}

const PrimaryButtonStyled = styled.a`
    background-color: var(--primary-color);
    padding: .8rem 2.5rem;
    color: white;
    cursor: pointer;
    display: inline-block;
    font-size: inherit;
    text-transform: uppercase;
    position: relative;
    transition: all .4s ease-in-out;
    &::after{
        content: "";
        position: absolute;
        width: 0;
        height: .2rem;
        transition: all .4s ease-in-out;
        left: 0;
        bottom: 0;
        opacity: .7;
    }
    &:hover::after{
        width: 100%;
        background-color: var(--white-color);
    }
`;
const ContactPageStyled = styled.section`
width: 100%;
    height: 100vh;
    position: relative;

    .typography{
        position: absolute;
        top: 25%;
        transform: translate(-3%, -86%);
        // text-align: center;
        width: 100%;
    }

    .contact-section{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 2rem;
        @media screen and (max-width: 978px){
            grid-template-columns: repeat(1, 1fr);
            .f-button{
                margin-bottom: 3rem;
            }
        }
        .right-content{
            display: grid;
            grid-template-columns: repeat(1, 1fr);
            @media screen and (max-width: 502px){
                width: 70%;
            }
        }
        
        .contact-title{
            h4{
                color: var(--white-color);
                padding: 1rem 0;
                font-size: 1.8rem;
            }
        }
        .form{
            width: 100%;
            @media screen and (max-width: 502px){
                width: 100%;
            }
            .form-field{
                margin-top: 2rem;
                position: relative;
                width: 100%;
                label{
                    position: absolute;
                    left: 20px;
                    top: -19px;
                    display: inline-block;
                    background-color: var(--background-dark-color);
                    padding:0 .5rem;
                    color: inherit;
                }
                input{
                    border: 1px solid var(--border-color);
                    outline: none;
                    background: transparent;
                    height: 50px;
                    padding:0 15px;
                    width: 100%;
                    color: inherit;
                }
                textarea{
                    background-color: transparent;
                    border: 1px solid var(--border-color);
                    outline: none;
                    color: inherit;
                    width: 100%;
                    padding: .8rem 1rem;
                }
            }

            
        }
    }
`;

export default ContactPage
